import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const vuexStore = new Vuex.Store({
  // 全局访问的state对象
  state: {
    count: 0, // 初始值
    changeShowCom: true
  },
  
  // 实时监听state值的变化（最新状态）
  getters: {
    // 承载变化的count值
    getCount (state) {
      return state.count
    },
    
    // 承载变化的showCom值
    isShow (state) {
      return state.changeShowCom
    }
  },
  
  mutations: {
    addCount (state, num) {
      state.count = state.count + num
    },
    
    delCount (state, num) {
      state.count > 0? state.count = state.count - num: state.count = 0      
    },
    
    show (state) {
      state.changeShowCom = true
    },
    
    hide (state) {
      state.changeShowCom = false
    }
  }
})

export default vuexStore



